import Axios from "axios";

Axios.create({
  headers: {
    "Content-Type": "application/json; charset=utf-8"
  },
  timeout: 5000
});

Axios.defaults.baseURL = process.env.API_HOST;
//  
Axios.interceptors.request.use(function(config) {
  return config;
});
//  
Axios.interceptors.response.use(
  function(response) {
    return response;
  },
  function(err) {
    if (err && err.response) {
      switch (err.response.status) {
        case 400:
          err.message = "(400)";
          break;
        case 401:
          err.message = "(401)";
          break;
        case 403:
          err.message = "(403)";
          break;
        case 404:
          err.message = "(404)";
          break;
        case 408:
          err.message = "(408)";
          break;
        case 500:
          err.message = "(500)";
          break;
        case 501:
          err.message = "(501)";
          break;
        case 502:
          err.message = "(502)";
          break;
        case 503:
          err.message = "(503)";
          break;
        case 504:
          err.message = "(504)";
          break;
        case 505:
          err.message = "HTTP(505)";
          break;
        default:
          err.message = `(${err.response.status})!`;
      }
    } else {
      err.message = "!";
    }
    message.error(err.message);
    return Promise.reject(err);
  }
);
export default Axios;



